import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    getDenuncias(ctx, { queryParams }) {
      return new Promise((resolve, reject) => {
        axios
          .get('/servicio-social/prevencion-hostigamiento/denuncias', { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

    postRegistroPrecalificar(ctx, { denunciaId, data }) {
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-social/prevencion-hostigamiento/denuncias/${denunciaId}/precalificar`, data)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

    updateArchivosDenuncia(ctx, { denunciaId, data }) {
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-social/prevencion-hostigamiento/denuncias/${denunciaId}`, data)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    downloadPdfPrecalificar(ctx, denunciaId) {
      console.log('url raiz:', window.location.host)
      return new Promise(() => {
        axios({
          url: `/servicio-social/prevencion-hostigamiento/denuncias/${denunciaId}/precalificar_pdf`,
          method: 'GET',
          responseType: 'blob', // important
        }).then(response => {
          const url = window.URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.setAttribute('href', `data:text/plain;charset=utf-8,${encodeURIComponent(response.data)}`)

          link.style.display = 'none'
          link.href = url
          link.setAttribute('download', 'file.pdf')
          link.setAttribute('target', '_blank')

          document.body.appendChild(link)
          link.click()
          document.body.removeChild(link)
        })
      })
    },

  },

}
