<template>

  <div>
    <ModalFormularioRegistroDenuncia @actualizarTablaDenuncias="actualizarTablaDenuncias" />
    <ModalCargarArchivoDenuncia @actualizarTablaDenuncias="actualizarTablaDenuncias" />
    <ModalActualizarEstado @actualizarTablaDenuncias="actualizarTablaDenuncias" />

    <b-card
      no-body
      class="mb-0 px-2"
    >
      <div class="m-2">

        <b-row>

          <b-col
            cols="12"
            md="6"
            class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
          >
            <label>Mostrar</label>
            <b-form-select
              v-model="perPage"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              :options="pageOptions"
              :clearable="false"
              class="per-page-selector d-inline-block mx-50"
            />
            <label>registros</label>
          </b-col>

          <b-col
            cols="12"
            md="6"
          >
            <div class="d-flex align-items-center justify-content-end">
              <b-form-input
                v-model="filter"
                size="md"
                type="search"
                class="d-inline-block mr-1"
                placeholder="Buscar..."
                debounce="400"
              />

            </div>
          </b-col>
        </b-row>

      </div>

      <b-table
        id="my-table"
        ref="refTablaDenuncias"
        class="position-relative"
        show-empty
        head-variant="light"
        hover
        small
        responsive
        :busy.sync="isBusy"
        :items="myProvider"
        :fields="tableColumns"
        :current-page="currentPage"
        :empty-text="'No hay registros para mostrar'"
        :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
        :per-page="perPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by="sortBy"
        :sort-desc="sortDesc"
        :height="myProvider.length < 5 ? '200px' : null"
      >

        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle" />
            <strong>Cargando...</strong>
          </div>
        </template>

        <template #cell(index)="data">
          {{ from + data.index }}
        </template>

        <template #cell(hostigamiento)="data">
          <b-badge
            pill
            :variant="`light-${resolveEstadoVariant(data.item.hostigamiento)}`"
            class="text-capitalize"
          >
            {{ data.item.hostigamiento }}
          </b-badge>
        </template>

        <template #cell(pre_calificar)="data">
          <div v-if="data.item.archivo_precalificar !== null">
            <b-button
              variant="outline-warning"
              size="sm"
              block
              :href="data.item.archivo_precalificar"
              :target="'_blank'"
            >pre calificar
              <feather-icon
                icon="ClipboardIcon"
                size="12"
                class="align-middle"
              />
            </b-button>
          </div>
        </template>

        <template #cell(archivo_resolucion)="data">
          <div v-if="data.item.archivo_resolucion !== null">
            <b-button
              variant="outline-success"
              size="sm"
              block
              :href="data.item.archivo_resolucion"
              :target="'_blank'"
            >resolución
              <feather-icon
                icon="ClipboardIcon"
                size="12"
                class="align-middle"
              />
            </b-button>
          </div>
        </template>

        <template #cell(accion)="data">
          <b-dropdown
            variant="link"
            no-caret
            :right="$store.state.appConfig.isRTL"
          >
            <template #button-content>
              <feather-icon
                icon="MoreVerticalIcon"
                size="18"
                class="align-middle text-primary"
              />
            </template>
            <b-dropdown-item
              @click="formularioPrecalificar(data.item)"
            >
              <feather-icon icon="EyeIcon" />
              <span class="align-middle ml-50">Formulario pre calificar</span>
            </b-dropdown-item>
            <b-dropdown-item
              @click="cargarArchivoPrecalificar(data.item)"
            >
              <feather-icon icon="EyeIcon" />
              <span class="align-middle ml-50">Cargar archivo pre calificar</span>
            </b-dropdown-item>
            <b-dropdown-item
              @click="cargarArchivoResolucion(data.item)"
            >
              <feather-icon icon="EyeIcon" />
              <span class="align-middle ml-50">Cargar archivo resolución</span>
            </b-dropdown-item>
            <b-dropdown-item
              @click="actualizarEstadoDenuncia(data.item)"
            >
              <feather-icon icon="EyeIcon" />
              <span class="align-middle ml-50">Actualizar estado</span>
            </b-dropdown-item>
          </b-dropdown>
        </template>

        <!-- Column: estado -->
        <!-- <template #cell(estado)="data">
          <b-badge
            pill
            :variant="`light-${resolveEstadoVariant(data.item.estado)}`"
            class="text-capitalize"
          >
            {{ data.item.estado }}
          </b-badge>
        </template> -->

        <!-- Incripcion -->
        <!-- <template #cell(inscripcion)="data">
          <span class="text-primary"><strong>Inicio: </strong></span>{{ new Intl.DateTimeFormat('es-PE',{month: 'long', day:'numeric', year:'numeric', hour:'numeric', minute: 'numeric'}).format(new Date(data.item.fecha_inicio)) }} <br>
          <span class="text-primary"><strong>Fin: </strong></span>{{ new Intl.DateTimeFormat('es-PE',{month: 'long', day:'numeric', year:'numeric', hour:'numeric', minute: 'numeric'}).format(new Date(data.item.fecha_fin)) }} <br>
        </template> -->

        <!-- Column: estado -->
        <!-- <template #cell(accion)="data">
          <div v-if="data.item.estado == 'borrador' || data.item.estado == 'vigente'">
            <b-button
              :variant="data.item.estado !== 'vigente' ? 'secondary' : 'info'"
              size="sm"
              block
              :disabled="data.item.estado !== 'vigente' ? true : false"
              @click="postularConvocatoria(data.item.id)"
            >Postular
              <feather-icon
                icon="ArrowRightIcon"
                size="12"
                class="align-middle text-white"
              />
            </b-button>
          </div>
          <div v-else-if="data.item.estado == 'concluido'">
            <b-button
              variant="outline-info"
              size="sm"
              block
              :href="data.item.archivo_resultado"
              :target="'_blank'"
            >Resultados
              <feather-icon
                icon="ClipboardIcon"
                size="12"
                class="align-middle"
              />
            </b-button>
          </div>
        </template> -->
      </b-table>
      <div class="mx-2 mb-2">
        <b-row>
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-start"
          >
            <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>
          </b-col>
          <!-- Pagination -->
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >
            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>
          </b-col>
        </b-row>
      </div>
    </b-card>
    <!-- <convocatoria-list-postulantes /> -->
  </div>
</template>

<script>
import {
  BCard, BRow, BCol, BFormInput, BTable, BSpinner, BFormSelect, BDropdown, BDropdownItem,
  BPagination, BButton, BBadge,
} from 'bootstrap-vue'
import { onUnmounted } from '@vue/composition-api'
import { avatarText } from '@core/utils/filter'
import store from '@/store'
import Ripple from 'vue-ripple-directive'
// import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import buzonStoreModule from './denunciasStoreModule'
import ModalFormularioRegistroDenuncia from './ModalFormularioRegistroDenuncia.vue'
import ModalCargarArchivoDenuncia from './ModalCargarArchivoDenuncia.vue'
import ModalActualizarEstado from './ModalActualizarEstado.vue'

export default {
  components: {
    // ConvocatoriasListFilters,
    // UserListAddNew,
    // ConvocatoriaListPostulantes,
    BDropdown,
    BDropdownItem,
    BCard,
    BRow,
    BCol,
    BFormInput,
    BTable,
    BSpinner,
    // BMedia,
    // BAvatar,
    // BLink,
    BBadge,
    // BDropdown,
    // BDropdownItem,
    BPagination,
    BButton,
    BFormSelect,

    // externs
    ModalFormularioRegistroDenuncia,
    ModalCargarArchivoDenuncia,
    ModalActualizarEstado,
    // ComunicadosModal,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      isBusy: false,
      from: 1,
      to: 1,
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, 50],
      sortBy: 'id',
      sortDesc: false,
      // sortDirection: 'desc',
      filter: '',
      filterOn: [],

    }
  },
  methods: {
    actualizarTablaDenuncias() {
      console.log('ENTRAMOS A ACTUALIZAR TABLA DENUNCIAS')
      this.$refs.refTablaDenuncias.refresh()
    },
    async myProvider(ctx) {
      // console.log('CTX: ', ctx)
      const promise = store.dispatch('denuncias-store-module/getDenuncias', { queryParams: ctx })
      // const promise = this.$http.get('/servicio-social/beca/convocatoria', { queryParams: ctx })
      // Must return a promise that resolves to an array of items
      return promise.then(response => {
        // console.log('responseBuzonDenuncias: ', response)
        const {
          data, total, from, to,
        } = response.data
        // Pluck the array of items off our axios response
        const items = data
        this.totalRows = total || 0
        // this.from = meta.pagination.count
        this.from = from || 0
        this.to = to || 0

        return items || []
      })
    },
    formularioPrecalificar(item) {
      this.$emit('abrirModalPrecalificar', item)

      // const DATA = {
      //   formulario_id: item.formulario_id,
      //   usuario_anonimo: item.usuario_anonimo,
      // }
      // store.dispatch('denuncias-store-module/pasarAdenuncias', DATA)

      //   .then(() => {
      //     this.$toast({
      //       component: ToastificationContent,
      //       position: 'top-right',
      //       props: {
      //         title: 'Registro pasado a denuncias.',
      //         icon: 'CheckCircleIcon',
      //         variant: 'success',
      //       },
      //     })
      //     this.actualizarTabla()
      //   })
    },
    cargarArchivoPrecalificar(item) {
      const itemData = item
      itemData.tituloModal = 'Pre calificar'
      itemData.rutaPost = 'postArchivoPrecalificar'

      this.$emit('abrirModalArchivoDenuncia', itemData)
    },
    cargarArchivoResolucion(item) {
      const itemData = item
      itemData.tituloModal = 'resolución'
      itemData.rutaPost = 'postArchivoResolucion'
      this.$emit('abrirModalArchivoDenuncia', itemData)
    },
    actualizarEstadoDenuncia(item) {
      this.$emit('abrirModalActualizarEstado', item)
    },

  },
  setup() {
    const DENUNCIAS_APP_STORE_MODULE_NAME = 'denuncias-store-module'

    // Register module
    if (!store.hasModule(DENUNCIAS_APP_STORE_MODULE_NAME)) store.registerModule(DENUNCIAS_APP_STORE_MODULE_NAME, buzonStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(DENUNCIAS_APP_STORE_MODULE_NAME)) store.unregisterModule(DENUNCIAS_APP_STORE_MODULE_NAME)
    })

    // const isAddNewUserSidebarActive = ref(false)

    const roleOptions = [
      { label: 'Comedor', value: 'Comedor' },
      { label: 'Residencia', value: 'Residencia' },
      { label: 'Bolsa de trabajo', value: 'Bolsa de trabajo' },
    ]

    const tableColumns = [

      { key: 'index', label: 'N°' },
      { key: 'denunciante', label: 'denunciante', sortable: true },
      { key: 'denunciado', label: 'denunciado', sortable: true },
      { key: 'pre_calificar', label: 'precalificar', sortable: true },
      { key: 'archivo_resolucion', label: 'resolución', sortable: true },
      {
        key: 'hostigamiento', label: 'hostigamiento', sortable: true, class: 'text-center',
      },
      { key: 'accion', label: 'acción', sortable: true },
    ]

    const resolveEstadoVariant = status => {
      if (status === 'PENDIENTE') return 'warning'
      if (status === 'NO') return 'success'
      if (status === 'SI') return 'danger'

      return 'primary'
    }
    const resolveServicioVariant = value => {
      if (value === 'BECA-COMEDOR') return 'primary'
      if (value === 'BECA-RESIDENCIA') return 'success'
      if (value === 'BECA-BOLSA') return 'info'
      return 'primary'
    }
    const resolveServicioIcon = value => {
      if (value === 'BECA-COMEDOR') return 'CoffeeIcon'
      if (value === 'BECA-RESIDENCIA') return 'HomeIcon'
      if (value === 'BECA-BOLSA') return 'BriefcaseIcon'

      return 'UserIcon'
    }
    // const {
    //   fetchUsers,
    //   tableColumns,
    //   perPage,
    //   currentPage,
    //   totalUsers,
    //   dataMeta,
    //   perPageOptions,
    //   searchQuery,
    //   sortBy,
    //   isSortDirDesc,
    //   refUserListTable,
    //   refetchData,

    //   // UI
    //   resolveServicioVariant,
    //   resolveServicioIcon,
    //   resolveEstadoVariant,

    //   // Extra Filters
    //   roleFilter,
    //   planFilter,
    //   statusFilter,
    // } = useConvocatoriasList()

    return {
      buzonStoreModule,
      // isAddNewUserSidebarActive,

      // fetchUsers,
      tableColumns,
      // perPage,
      // currentPage,
      // totalUsers,
      // dataMeta,
      // perPageOptions,
      // searchQuery,
      // sortBy,
      // isSortDirDesc,
      // refUserListTable,
      // refetchData,

      avatarText,

      resolveServicioVariant,
      resolveServicioIcon,
      resolveEstadoVariant,

      roleOptions,

      // roleFilter,
      // planFilter,
      // statusFilter,
    }
  },
}
</script>

<style lang="scss" scoped>
.per-page-selector {
  width: 90px;
}
</style>

<style lang="scss">
// @import '@core/scss/vue/libs/vue-select.scss';
</style>
